.inputText {
  display: flex;
  flex-direction: column;
  gap: 7px;
  width: 100%;

  span {
    font-weight: 400;
    font-size: 12px;

    color: #757479;
  }
  input,
  select {
    background: #f3f3f5;
    border-radius: 5px;
    width: 100%;
    padding: 14px 12px;
    font-size: 16px;
    color: black;
  }

  select {
    padding: 12px;
  }
}
