.singup {
  &__content {
    max-width: 433px;
    button {
      margin-top: 60px;
      width: 100%;
    }
  }
  &__description {
    padding-bottom: 32px;
  }
  &__title {
    font-family: 'Golos Text', sans-serif;

    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    color: #131313;
    padding-bottom: 16px;
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;

    color: #5f5e64;
    &-action {
      color: #131313;
      text-decoration: underline;
      cursor: pointer;

      &:hover {
        opacity: 0.8;
      }
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  .lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 24px;
  }
  .lds-ellipsis div {
    position: absolute;
    top: 4px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: white;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
  }
  .lds-ellipsis div:nth-child(1) {
    left: 8px;
    animation: lds-ellipsis1 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(2) {
    left: 8px;
    animation: lds-ellipsis2 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(3) {
    left: 32px;
    animation: lds-ellipsis2 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(4) {
    left: 56px;
    animation: lds-ellipsis3 0.6s infinite;
  }
  @keyframes lds-ellipsis1 {
    0% {
      transform: scale(0);
    }
    100% {
      transform: scale(1);
    }
  }
  @keyframes lds-ellipsis3 {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(0);
    }
  }
  @keyframes lds-ellipsis2 {
    0% {
      transform: translate(0, 0);
    }
    100% {
      transform: translate(24px, 0);
    }
  }
  
}
